import { createStore } from 'vuex'
export default createStore({
  //æ•°æ®ç®¡ç†æ¡†æž¶ï¼Œæ–¹ä¾¿æ•°æ®å¤šå±‚çº§ä¼ è¾“ï¼Œåˆ›å»ºå…¨å±€å”¯ä¸€ä»“åº“ï¼Œç”¨æ¥å­˜æ”¾å…¨å±€çš„æ•°æ®
  state: {
    //  Home View States
    mainFunctionsVisibility:true,
    //  IndentificationCard States
    Span:false,
    // User Login States
    loginState: false,
    userEmail: '',
    // Current Section
    currentSection: '',
    //table delete index
    currentDeleteIndex: {value: -1},
    currentPostIndex: {value: -1}
  },
  getters: {
  },
  mutations: {
  //  Home View Functions
    changeMainFunctionsInvisible(){
      this.state.mainFunctionsVisibility = false;
    },
    changeMainFunctionsVisible(){
      this.state.mainFunctionsVisibility = true;
    },
    changeSpanState(){
      this.Span = !this.Span;
    },
    userLogin(state, email){
      state.loginState = true;
      state.userEmail = email;
    },
    userLogout(){
      this.state.loginState = false;
      this.state.userEmail = null;
    },
    setCurrentSection(state, section){
      state.currentSection = section;
    },
    changeCurrentDeletion(state, indexFinal){
      this.state.currentDeleteIndex = {value: indexFinal};
    },
    changeCurrentPost(state, indexFinal){
      this.state.currentPostIndex = {value: indexFinal};
    }
  },
  actions: {
  },
  modules: {
  }
})